<template>
  <b-container fluid>
    <b-row>
      <b-col>
         <b-card title="Process monitor & controll for Mcc"
          sub-title="     .">
        </b-card>
      </b-col>
    </b-row>
    <b-row class="mt-3">
      <b-col>
        <b-card-group deck class="mb-3">
          <mcc-program
           pmname="rabbitmq"
           ip="0.0.0.0"
           port="5672"></mcc-program>
          <mcc-program
           pmname="mccgateway"
           ip="0.0.0.0"
           port="7088"></mcc-program>
          <mcc-program
           pmname="mysql"
           ip="0.0.0.0"
           port="3306"></mcc-program>
        </b-card-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-card-group deck class="mb-3">
          <mcc-program
           pmname="elasticsearch"
           ip="0.0.0.0"
           port="9200"></mcc-program>
          <mcc-program
           pmname="logstash"
           ip="0.0.0.0"
           port="9600"></mcc-program>
          <mcc-program
           pmname="kibana"
           ip="127.0.0.1"
           port="5601"></mcc-program>
        </b-card-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-card-group deck class="mb-3">
          <mcc-program
           pmname="mccdaemon.exe"
           ip=""
           port=""></mcc-program>
          <mcc-program
           pmname="filebeat.exe"
           ip=""
           port=""></mcc-program>
        </b-card-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
         <b-card title="  "
          sub-title="  " class="mt-3">
            <div class="mt-3">
              <h6>1) OS(Windows Server 2012)      </h6>
               Services   <b> MccLogviewer</b> ,<b> MccGateway</b> ,<b>  Elaticsearch </b>, <b> RabbitMQ</b>    <br/><br/>
              
              <h6>2) Mcc      </h6>
              - <b>Logstash</b> , <b>Filebeat</b> <br/>
              ** Logstash      .( 30,  1) <br/><br/>

              <h6>3) Mcc Gateway </h6>
              -  : java -jar MccGateway-1.0-SNAPSHOT.jar --spring.profiles.active=local <br/><br/>

              <h6>4) Daemon  </h6>
              -  <b>MCCDaemon.exe </b>
              </div>
          </b-card>
      </b-col>
    </b-row>
    
  </b-container>
</template>
<script>
  import MccProgram from './program.vue'
  export default {
    components: {
      MccProgram
    }
  }
</script>
<style>
</style>


